import React, { Component } from 'react';

export default class Resume extends Component {
  render() {
    return (
      <React.Fragment>
      <section id="resume">
  {/* Education
----------------------------------------------- */}
  <div className="row education">
    <div className="three columns header-col">
      <h1><span>Education</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="row item">
        <div className="twelve columns">
          <h3>University of Texas at Dallas, U.S.A</h3>
          <p className="info">Masters in Computer Science <span>•</span> <em className="date">May 2015</em></p>
          <p>
            Courses - Computer algorithms, operating sytems, computer networks, security, AI ... <br/>
            Projects - Video chat app, broadcasting system, university coursebook ...
          </p>
        </div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h3>University of Mumbai, India</h3>
          <p className="info">Bachelors in Electrical Engineering <span>•</span> <em className="date">May 2011</em></p>
          <p>
            Courses - computer arch, Real time systems, Microprocessors, neural networks, control systems ... <br/>
            Projects - Neural network based engine sensing and control.
          </p>
        </div>
      </div> {/* item end */}
    </div> {/* main-col end */}
  </div> {/* End Education */}
  {/* Work
----------------------------------------------- */}
  <div className="row work">
    <div className="three columns header-col">
      <h1><span>Work</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="row item">
        <div className="twelve columns">
          <h3>Salalah International Connections</h3>
          <p className="info">Full Stack Developer <span>•</span> <em className="date">August 2017 - Present</em></p>
          <p>
              Designing Single page web applications to serve the ERP requirements of mining companies.
              Applications built using MERN stack. 
          </p>
        </div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h3>United Nations</h3>
          <p className="info">Software Engineer <span>•</span> <em className="date">October 2015 - April 2017</em></p>
          <p>
            Designed and developed highly interactive issue reporting and troubleshooting 
            application for UN’s users around the globe. 
            Administered UN’s enterprise social collaboration platform (Unite Connections),
            providing high-level system analysis and 24x7 technical support.
          </p>
        </div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h3>Ericsson Inc.</h3>
          <p className="info">Software Engineer<span>•</span> <em className="date">August 2014 - May 2015</em></p>
          <p>
             Designed and implemented a forecasting tool to analyze the demand, supply, and revenue. Involved in business application development.
          </p>
        </div>
      </div> {/* item end */}
    </div> {/* main-col end */}
  </div> {/* End Work */}
  {/* Skills
----------------------------------------------- */}
  <div className="row skill">
    <div className="three columns header-col">
      <h1><span>Skills</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="bars">
        <ul className="skills">
          <li><span className="bar-expand photoshop" /><em>React</em></li>
          <li><span className="bar-expand illustrator" /><em>Node</em></li>
          <li><span className="bar-expand wordpress" /><em>Express</em></li>
          <li><span className="bar-expand css" /><em>MongoDB</em></li>
          <li><span className="bar-expand html5" /><em>Java</em></li>
          <li><span className="bar-expand jquery" /><em>JavaScript</em></li>
        </ul>
      </div>{/* end skill-bars */}
    </div> {/* main-col end */}
  </div> {/* End skills */}
</section> {/* Resume Section End*/}

    
      </React.Fragment>
    );
  }
}