import React, { Component } from 'react';
import * as emailjs from 'emailjs-com'

export default class ContactUs extends Component {

  constructor(props){
    super(props)
    this.state={
      submit_message : ''
    }
    this.handleSubmit=this.handleSubmit.bind(this)
  }
  

  handleSubmit(e){
    e.preventDefault();
    const form= document.forms.contact

    // console.log("in submit", form.contactName.value, form.contactMessage.value, form.contactEmail.value)
    var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    if (form.contactName.value === '' || form.contactEmail.value === '' || form.contactMessage.value === ''){
        this.setState({submit_message : 'empty'})
    }
    else if(! re.test(form.contactEmail.value))
        this.setState({submit_message : 'email invalid'})
    else{

      var templateParams ={
        from_name : form.contactName.value + ' (' + form.contactEmail.value + ')',
        to_name: 'afifpatel.aag@gmail.com',
        subject: form.contactSubject.value,
        message_html : form.contactMessage.value
      }

      emailjs.send('gmail', 'template_ouvFMTnt', templateParams, 'user_faxhAZ1fHs999686Z7kv8')
      .then(function(response){
        console.log('SUCCESS!', response.status,response.text)
      }, function(err){
        console.log(err)
      });
      this.setState({submit_message : 'ok'})
    // console.log("in submit", form)
    }
  }

  renderMessage(message){
    switch(message){
      case 'empty':{
        return <div id="warning" > Field marked with * are mandatory</div>
      }
      case 'email invalid':{
        return <div id="warning" > Email is invalid</div>
      }
      case 'ok':{
        return <div id="success"><i className="fa fa-check" />Your message was sent, thank you!<br /></div>
      }
      default:
      return null
    }
  }

  render() {
    return (
      <React.Fragment>
                {/* Contact Section
   ================================================== */}
<section id="contact">
  <div className="row section-head">
    <div className="two columns header-col">
      <h1><span>Get In Touch.</span></h1>
    </div>
    <div className="ten columns">
      <p className="lead">Happy to help yaaa!!!! <br /> Need a demo? <br /> Have an inquiry? <br /> Please fill out the form or call me.
      </p>
    </div>
  </div>
  <div className="row">
    <div className="eight columns">
      {/* form */}
      <form  onSubmit={this.handleSubmit} name="contact">
        <fieldset>
          <div>
            <label htmlFor="contactName">Name <span className="required">*</span></label>
            <input type="text" size={35} id="contactName" name="contactName" />
          </div>
          <div>
            <label htmlFor="contactEmail">Email <span className="required">*</span></label>
            <input type="text" size={35} id="contactEmail" name="contactEmail" />
          </div>
          <div>
            <label htmlFor="contactSubject">Subject</label>
            <input type="text" size={35} id="contactSubject" name="contactSubject" />
          </div>
          <div>
            <label htmlFor="contactMessage">Message <span className="required">*</span></label>
            <textarea cols={50} rows={15} id="contactMessage" name="contactMessage" defaultValue={""} />
          </div>
          <div>
            <button className="submit">Submit</button>
            <span id="image-loader">
              <img    src="images/loader.gif" />
            </span>
          </div>
        </fieldset>
      </form> {/* Form End */}
      {/* contact-warning */}
      {this.renderMessage(this.state.submit_message)}
    </div>
    <aside className="four columns footer-widgets">
      <div className="widget widget_contact">
        <h4>Address and Phone</h4>
        <p className="address">
          Afif Patel<br />
          City Center <br />
          Salalah, Oman<br />
          <span>(+968) 9387-9435</span>
        </p>
      </div>
    </aside>
  </div>
</section> {/* Contact Section End*/}

      </React.Fragment>
    )
  }
}