import React, { Component } from 'react';

export default class About extends Component {
  render() {
    return (
      <React.Fragment>
        
          <section id="about">
        <div className="row">
          <div className="three columns">
            <img className="profile-pic" src="images/dp.jpg"  />
          </div>
          <div className="nine columns main-col">
            <h2>About Me</h2>
            <p>Hi, I'm Afif Patel, a full stack developer with 4+ years of experience and a Masters Degree in Computer Science. 
               I am an Indian citizen living in Oman. At my current project, I am responsible of building single-page applications
               for various clients. The development stack at the moment is MERN - React, Redux, Node, Express, MongoDB. 
               However, from previous projects I have derived an excellent experience with other stacks mainly Java, SQL, Apache HTTP and PHP.
               Apart from work, I enjoy learning new technologies and implementing them on my side projects.
               I have excellent communication skills. I love playing soccer, swimming and meditating.
               </p>
            <div className="row">
              <div className="columns contact-details">
                <h2>Contact Details</h2>
                <p className="address">
                  <span>Afif Patel</span><br />
                  <span>City Center<br />
                    Salalah, Sultanate of Oman 
                  </span><br />
                  <span>(+968) 9387-9435</span><br />
                  <span>afifpatel.aag@gmail.com</span>
                </p>
              </div>
              <div className="columns download">
                <p>
                  <a href="/Resume_Afif_Patel.docx" className="button"><i className="fa fa-download" />Download Resume</a>
                </p>
              </div>
            </div> {/* end row */}
          </div> {/* end .main-col */}
        </div>
      </section> {/* About Section End*/}

      </React.Fragment>
    )
  }
}
